.sort {
  display: flex;
  align-items: center;
  font-size: 15px;
  font-weight: 600;
  margin: 24px -22px;
  justify-content: flex-end;
  @include media-breakpoint-down(md) {
    justify-content: flex-start;
    flex-wrap: wrap;
    font-size: 14px;
  }
  .sort__item_asc {
    background-color: rgb(0, 0, 0);
    width: 8px;
    height: 2px;
    position: relative;
    margin-left: 16px;
    display: block;
    @include media-breakpoint-down(xs) {
      margin-left: 8px;
    }
    &:before {
      content: '';
      background-color: rgb(0, 0, 0);
      position: absolute;
      top: -4px;
      left: 0;
      width: 5px;
      height: 2px;
    }
    &:after {
      content: '';
      background-color: rgb(0, 0, 0);
      position: absolute;
      bottom: -4px;
      left: 0;
      width: 11px;
      height: 2px;

    }
  }
  .sort__item_desc {
    background-color: rgb(0, 0, 0);
    width: 8px;
    height: 2px;
    position: relative;
    margin-left: 16px;
    display: block;
    @include media-breakpoint-down(xs) {
      margin-left: 8px;
    }
    &:before {
      content: '';
      background-color: rgb(0, 0, 0);
      position: absolute;
      top: -4px;
      left: 0;
      width: 11px;
      height: 2px;
    }
    &:after {
      content: '';
      background-color: rgb(0, 0, 0);
      position: absolute;
      bottom: -4px;
      left: 0;
      width: 5px;
      height: 2px;

    }
  }

}

.sort__item {
  padding: 6px 22px;
  color: #808080;
  position: relative;
  display: flex;
  align-items: center;
  > input[type="radio"]{
    visibility: hidden;
    display: none;
    &:checked + label{
      color: $black;
    }
    &[data-sort='desc']:checked + label{
      span{
        background-color: rgb(0, 0, 0);
        width: 8px;
        height: 2px;
        position: relative;
        margin-left: 16px;
        display: block;
        @include media-breakpoint-down(xs) {
          margin-left: 8px;
        }
        &:before {
          content: '';
          background-color: rgb(0, 0, 0);
          position: absolute;
          top: -4px;
          left: 0;
          width: 11px;
          height: 2px;
        }
        &:after {
          content: '';
          background-color: rgb(0, 0, 0);
          position: absolute;
          bottom: -4px;
          left: 0;
          width: 5px;
          height: 2px;

        }
      }
    }
    &[data-sort='asc']:checked + label{
      span{
        background-color: rgb(0, 0, 0);
        width: 8px;
        height: 2px;
        position: relative;
        margin-left: 16px;
        display: block;
        @include media-breakpoint-down(xs) {
          margin-left: 8px;
        }
        &:before {
          content: '';
          background-color: rgb(0, 0, 0);
          position: absolute;
          top: -4px;
          left: 0;
          width: 5px;
          height: 2px;
        }
        &:after {
          content: '';
          background-color: rgb(0, 0, 0);
          position: absolute;
          bottom: -4px;
          left: 0;
          width: 11px;
          height: 2px;

        }
      }
    }
  }
  > label{
    margin:0;
    cursor: pointer;
    padding:0;
    position: relative;
    display: flex;
    align-items: center;
    color: #808080;
  }
  @include media-breakpoint-down(xs) {
    padding: 6px 16px;
  }
  &_title {
    color: $black;
    @include media-breakpoint-down(md) {
      width: 100%;
    }
  }
  &_active {
    color: $black;
  }
}

.filter {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  margin: 0 -10px;
  padding-bottom:10px;
  flex-wrap: wrap;
  border-bottom: 1px solid rgb(232, 232, 232);
  .filter-item {
    padding: 10px;
    @include media-breakpoint-down(xs) {
      padding: 6px;
      flex:1 1 auto;
    }
  }
  .dropdown-toggle::after {
    content: none;
  }
  .bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn) {
    width: auto;
    @include media-breakpoint-down(xs) {
      width: 100%;
    }
  }
  .bootstrap-select .dropdown-toggle .filter-option-inner-inner{
    text-align: center;
  }
  .bootstrap-select > .dropdown-toggle {
    background: #efefef;
    color: $black;
    font-size: 15px;
    font-weight: 600;
    @include media-breakpoint-down(md) {
      font-size: 14px;
    }
  }
  .bootstrap-select > .dropdown-toggle,
  .bootstrap-select > .dropdown-toggle:active,
  .bootstrap-select > .dropdown-toggle:focus,
  .bootstrap-select > .dropdown-toggle:hover {
    background: #efefef;
    color: $black;
    box-shadow: none !important;
    border: none !important;
    outline: none !important;
  }
}
.dropdown-item {
  font-size: 15px;
  box-shadow: none !important;
  border: none !important;
  outline: none !important;
  @include media-breakpoint-down(md) {
    font-size: 14px;
  }
}
.dropdown-item.active, .dropdown-item:active {
  color:  $black;
  text-decoration: none;
  background-color: #efefef;
}
.filter-select-button {
    background: #efefef;
    color: #000;
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
    border: none !important;
    outline: none !important;
    padding: 12px 28px;
    height: 40px;
    p {
        font-size: 0.9375rem;
        line-height: 100%;
        font-weight: 600;
        margin: 0;
    }
    @include media-breakpoint-down(xs) {
        height: 36px
      }
}
.off-canvas {
    @include media-breakpoint-down(md) {
        position: fixed;
        left: 0;
        top: 0;
        bottom: 0;
        width: 300px !important;
        z-index: 12;
        background: #fff;
        margin: 0 !important;
        padding-top: 10px;
        overflow: scroll;
        visibility: visible;
        transition: all .3s ease-in-out;

    }
}
#closeCollection {

        background: #fff;
        color: #ccc;
        transform-origin: -200% 0;
        -webkit-box-shadow: none !important;
        box-shadow: none !important;
        border: none !important;
        outline: none !important;
        padding: 12px ;
        display: block;
        margin-left: auto;
        transform: translateX(0);
        span {
            font-size: 35px;
        }



}
.collection-hidden {
    @include media-breakpoint-down(md) {
        transform: translateX(-200%);
        //transition: all 1s cubic-bezier(1, .9, 0, 0);
    }

}
