.delivery {
    padding-top: 70px;
    .cart-title {
        margin-bottom: 54px;
    }
    .form-wrapper {
        display: flex;
        padding-bottom: 70px;
        border-bottom: 1px solid #e8e8e8;
        .form-delivery {
            display: flex;
            flex-direction: row;
            @media screen and (max-width: 1410px) {
                justify-content: space-between;
                flex: 1 1 100%;
            }
            @media screen and (max-width: 992px) {
                flex-direction: column;
            }
            .form-group {
                margin-bottom: 0;
                &:first-child {
                    margin-left: 30px;
                    margin-right: 235px;
                    @media screen and (max-width: 1410px) {
                        margin-left: 0;
                        margin-right: 0;
                    }
                    @media screen and (max-width: 992px) {
                        margin-bottom: 30px;
                    }
                }
                .checkRadio {
                    display: none;
                }
                .checkRadioMark {
                    position: relative;
                    display: flex;
                    align-items: center;
                    font-size: 18px;
                    font-weight: 700;
                    line-height: 1;
                    padding-left: 70px;
                    margin-bottom: 25px;
                    height: 30px;

                    &:before {
                        position: absolute;
                        content: ' ';
                        display: block;
                        width: 30px;
                        height: 30px;
                        border-radius: 3px;
                        border: 2px solid rgb(179, 179, 179);
                        left: 0;
                    }
                }
                .checkRadio:checked + .checkRadioMark:before {
                    background-image: url('../img/content/cart/checkmark.svg');
                    background-repeat: no-repeat;
                    background-position: 50% 50%;
                    display: flex;
                    background-size: 16px auto;
                    align-items: center;
                    justify-content: center;
                }
                .choice-another {
                    font-size: 15px;
                    font-weight: 600;
                    color: #000;
                    padding: 9px 31px;
                    border-radius: 3px;
                    background-color: #efefef;
                    border: none;
                    outline: none;
                    margin-top: 30px;
                    margin-left: 70px;
                    display: flex;
                    align-items: center;
                    @media screen and (max-width: 992px) {
                        margin-left: 0;
                    }
                }
                p {
                    font-size: 16px;
                    font-weight: normal;
                    margin-left: 70px;
                    margin-bottom: 0;
                    line-height: 1;
                    display: flex;
                    flex-direction: column;
                    @media screen and (max-width: 992px) {
                        margin-left: 0;
                    }
                    .choiceAd {
                        font-size: 16px;
                        margin-bottom: 15px;
                        line-height: 1;
                        &:first-child {
                            margin-bottom: 0;
                        }
                        &:last-child {
                            margin-bottom: 0;
                        }
                    }
                    .span-address {
                        font-weight: 600;
                        font-size: 20px;
                        line-height: 1;
                        margin-top: 27px;
                        margin-bottom: 20px;

                    }
                    .free {
                        color: #2fc62f;
                        margin-top: 32px;
                        &.not-free {
                            color: #c62f2f;
                            font-family: "Montserrat", sans-serif;
                            font-weight: 500;
                            span{
                                font-weight: 600;
                                font-family: "Open Sans", sans-serif
                            }

                        }
                    }
                }
            }
        }
    }
}
