.active-order {
  padding-top: 70px;
  &:last-child {
    padding-bottom: 100px;
    @media screen and (max-width: 630px) {
      padding-bottom: 0;
    }
  }
  .col-order {
    /*padding-right: 15px;
    padding-left: 0;*/
    @media screen and (max-width: 1300px) {
      max-width: 100%;
    }
    .order-title {
      border-radius: 3px;
      background-color: #efefef;
      padding: 20px 30px;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: center;
      margin-bottom: 30px;
      @media screen and (max-width: 455px) {
        flex-direction: column;
      }
      .num-order {
        font-size: 18px;
        font-weight: 700;
        color: #000;
      }
      .periodDeliv {
        margin-right: 0;
        margin-left: auto;
        font-size: 15px;
        font-weight: normal;
        color: #000;
        display: flex;
        flex-direction: row;
        align-items: center;
        @media screen and (max-width: 455px) {
          margin-left: 0;
          margin-top: 30px;
        }
        .ellipse {
          display: block;
          width: 15px;
          height: 15px;
          margin-right: 15px;
          border-radius: 50%;
          &.yellow {
            background-color: #dcc000;
          }
          &.green {
            background-color: #2ed900;
          }
        }
      }
    }
    .good-row {
      margin-right: 0;
      margin-left: 0;
      padding-bottom: 32px;
      margin-bottom: 34px;
      border-bottom: 1px solid #e8e8e8;
      .good-card {
        display: flex;
        flex: 1 1 auto;

        @include media-breakpoint-down(xs) {
          flex-direction: column;
        }
        .good-desc {
          display: flex;
          flex-direction: row;
          flex: 1 1 auto;
          @include media-breakpoint-down(md) {
            flex-wrap: wrap;
          }
          @include media-breakpoint-down(xs) {
            flex-direction: column;
            flex-wrap: nowrap;
          }
          .img-wrapper {
            width: 200px;
            height: 200px;
            flex: 0 0 200px;
            position: relative;
            margin-right: 55px;
            @include media-breakpoint-down(md) {
              width: 160px;
              height: 160px;
              flex: 0 0 160px;
              margin-right: 30px;
            }
            @include media-breakpoint-down(xs) {
              margin-right: 20px;
            }
            @include media-breakpoint-down(xs) {
              width: 100%;
              margin-bottom: 20px;
              margin-right: 0;
              display: flex;
              justify-content: center;
            }
            img {
              width: 100%;
              height: 100%;
              @include media-breakpoint-down(xs) {
                width: 200px;
              }
            }
          }
          .articul-row {
            display: flex;
            flex-direction: column;
            @include media-breakpoint-down(md) {
              flex: 1 1 auto;
            }
            .articul-title {
              display: flex;
              flex-direction: column;
              @include media-breakpoint-down(xs) {
                text-align: center;
              }
              span {
                &.t-bold {
                  text-transform: uppercase;
                  font-size: 22px;
                  font-weight: 700;
                  line-height: 1;
                  color: #000;
                }
                &.t-small {
                  margin-top: 10px;
                  font-weight: normal;
                  font-size: 15px;
                  color: #8d8d8d;
                }
              }
            }
            .articul-table {
              display: flex;
              flex-direction: row;
              margin-top: 36px;
              @include media-breakpoint-down(md) {
                justify-content: space-between;
                width: 100%;
              }
              @include media-breakpoint-down(xs) {
                flex: 1 1 auto;
                justify-content: center;
              }
              @media screen and (max-width: 410px) {
                flex-direction: column;
              }
              .table-row {
                display: flex;
                flex-direction: column;

                @include media-breakpoint-down(xs) {
                  margin: 10px 20px !important;
                  align-items: center;
                  text-align: center;
                }
                @media screen and (max-width: 410px) {
                  text-align: center;
                  align-items: center;
                  margin-bottom: 20px;
                  &:last-child {
                    margin-bottom: 0;
                  }
                }
                &:nth-child(2) {
                  margin-left: 100px;
                  margin-right: 100px;
                  @include media-breakpoint-down(md) {
                    margin-left: 0;
                    margin-right: 0;
                  }
                }
                .table-header-cell {
                  font-weight: 700;
                  font-size: 16px;
                  color: #000;
                  margin-bottom: 8px;
                  line-height: 1;
                }
                .table-cell {
                  font-size: 16px;
                  font-weight: normal;
                  color: #000;
                  .color {
                    width: 30px;
                    height: 30px;
                    display: block;
                    &.c-blue {
                      background-color: #43496a;
                    }
                    &.c-white {
                      background-color: #ccd4db;
                    }
                    &.c-beige {
                      background-color: #e2d9ce;
                    }
                  }
                }
              }
            }
            .good-price {
              display: flex;
              flex-direction: column;
              align-items: flex-start;
              margin-top: 25px;
              @media screen and (max-width: 630px) {
                align-items: center;
                text-align: center;
              }
              @media screen and (max-width: 410px) {
                flex-direction: column;/*
                align-items: flex-end;*/
              }
              .priceForGood {
                display: flex;
                flex-direction: column;
                @media screen and (max-width: 410px) {
                  margin-bottom: 20px;
                }
                .bold-price {
                  display: flex;
                  flex-direction: row;
                  align-items: flex-end;
                  margin-bottom: 22px;
                  @media screen and (max-width: 630px) {
                    align-items: center;
                    text-align: center;
                    justify-content: center;
                  }
                  @include media-breakpoint-down(xs) {
                    margin-bottom: 10px;
                  }
                  .price {
                    margin-right: 10px;
                    font-size: 26px;
                    font-weight: 700;
                    line-height: 1;
                  }
                  .ruble {
                    font-size: 24px;
                    font-weight: 600;
                    line-height: 1;
                  }
                }
                .priceForItem {
                  display: flex;
                  flex-direction: row;
                  align-items: flex-end;
                  .price {
                    margin-right: 8px;
                    font-size: 20px;
                    font-weight: 600;
                    color: #626262;
                  }
                  .ruble {
                    font-family: 'Montserrat', sans-serif;
                    font-size: 17px;
                    font-weight: 500;
                    color: #626262;
                  }
                }
              }
            }
          }
          .button-div {
            display: flex;
            align-items: flex-end;
            flex: 1 1 auto;
            justify-content: flex-end;
            margin-right: 0;
            margin-left: auto;
            @include media-breakpoint-down(md) {
              flex: 1 1 100%;
              margin-top: 20px;
            }
            @include media-breakpoint-down(xs) {
              justify-content: center;
            }

            @include media-breakpoint-down(xs) {
              margin-left: 0;

              button {
                /*width: 100%;*/
              }
            }
          }
        }
      }
    }
    .good-data {
      margin-bottom: 50px;
      .data-row {
        margin-right: 0;
        margin-left: 0;
        display: flex;
        flex-direction: column;
        padding-bottom: 70px;
        border-bottom: 1px solid #e8e8e8;
        .data-formalized {
          font-size: 16px;
          font-weight: normal;
          display: flex;
          align-items: center;
          margin-bottom: 25px;
          @media screen and (max-width: 380px) {
            font-size: 14px;
          }
        }
        .table-wrapper {
          display: flex;
          flex: 1 1 auto;
          flex-direction: row;
          @include media-breakpoint-down(xs) {
            flex-direction: column;
          }
          .order-table {
            display: flex;
            flex-direction: column;
            tr {
              display: grid;
              grid-template-columns: minmax(255px, 0.5fr) minmax(255px, 1fr);
              margin-bottom: 25px;
              @media screen and (max-width: 530px) {
                display: flex;
                justify-content: space-between;
              }
              &:last-child {
                margin-bottom: 0;
              }
              td {
                font-size: 16px;
                font-weight: normal;
                @media screen and (max-width: 380px) {
                  font-size: 14px;
                }
                &:last-child {
                  font-weight: 600;
                  @media screen and (max-width: 530px) {
                    text-align: right;
                  }
                }
                &.free {
                  color: #2fc62f;
                }
              }
            }
          }
          .button-div {
            display: flex;
            flex-direction: column;
            margin-right: 0;
            margin-left: auto;
            align-items: flex-end;
            @include media-breakpoint-down(xs) {
              margin-right: 0;
              margin-left: 0;
              align-items: flex-start;
              margin-top: 20px;
              button {
                width: 100%;
                padding-left: 0;
                padding-right: 0;
                text-align: center;
              }
            }
            .refuse {
              margin-bottom: 10px;
            }
          }
        }
      }

    }
  }
}
