.form-colors__item {
  padding: 0;
  width: 40px;
  height: 40px;
  position: relative;
  display: block;
  margin-right: 0.625rem;
  margin-top: 0.625rem;
  border: 1px solid transparent;
  flex: 0 0 auto;
  margin-left: 0 !important;
  position: relative;

  &:focus,
  &:active {
    box-shadow: none;
    outline: none;

  }
  &.active {
    border:1px solid #b3b3b3;
    box-shadow: inset 0 0 0 2px #fff !important;
