.catalog-filter {
  .btn-hide-filter {
    position: absolute;
    right: 10px;
    cursor: pointer;
    top: 10px;
    font-size: 36px;
  }
  @include media-breakpoint-down(md) {
    position: fixed;
    left: 0;
    top: 0;
    height: 100vh;
    overflow: auto;
    width: 100%;
    max-width: 525px;
    background: #fff;
    z-index: 1030;
    padding: 30px;
    transform: translateX(-100%);
    transition: all .3s;
    &.active {
      transform: none;
      transition: all .3s;
    }
    &::-webkit-scrollbar {
      width: 4px;
    }
    &::-webkit-scrollbar-button {
      background-color: #ffffff;
    }
    &::-webkit-scrollbar-track {
      background-color: #ffffff;
    }
    &::-webkit-scrollbar-track-piece {
      background-color: #ffffff;
    }
    &::-webkit-scrollbar-thumb {
      background-color: #e6e6e6;
      border-radius: 4px;
    }
    &::-webkit-scrollbar-corner {
      background-color: #ffffff;
    }
    &::-webkit-resizer {
      background-color: #ffffff;
    }
  }
  .form-group {
    h6 {
      margin-bottom: 1.5rem;
    }
  }
}

