.customer {
    padding-top: 51px;
    .cart-title {
        margin-bottom: 54px;
    }
    .form-wrapper {
        display: flex;
        padding-bottom: 40px;
        border-bottom: 1px solid #e8e8e8;
        .cartForm {
            display: flex;
            flex-direction: column;
            @media screen and (max-width: 510px) {
                width: 100%;
            }
            .form-group {
                margin-bottom: 32px;
                @media screen and (max-width: 510px) {
                    width: 100%;
                }
                &:nth-child(3) {
                    margin-bottom: 0;
                }
                &:last-child {
                    margin-bottom: 0;
                    margin-top: 30px;
                }
                &.row {
                    margin-right: 0;
                    margin-left: 0;
                }
                label {
                    font-size: 16px;
                    font-weight: 600;
                    line-height: 1;
                    margin-bottom: 0;
                    @media screen and (max-width: 510px) {
                        width: 100%;
                    }
                    .inp-div {
                        margin-top: 16px;
                        display: flex;
                        flex-direction: row;
                        @media screen and (max-width: 1000px) {
                            flex-direction: column;
                            display: flex;
                        }
                        @media screen and (max-width: 510px) {
                            width: 100%;
                        }
                        .form-control {
                            width: 460px;
                            height: 48px;
                            margin-right: 30px;
                            border-radius: 3px;
                            padding-left: 21px;
                            border: 1px solid #b2b2b2;
                            @media screen and (max-width: 1000px) {
                                margin-right: 0;
                            }
                            @media screen and (max-width: 510px) {
                                width: 100%;
                            }
                            &::placeholder {
                                font-size: 15px;
                                color: #b2b2b2;
                            }
                            &:last-child {
                                margin-right: 0;
                            }
                        }
                    }

                }
                .form-control {
                    width: 460px;
                    height: 48px;
                    margin-right: 30px;
                    border-radius: 3px;
                    padding-left: 21px;
                    margin-top: 16px;
                    border: 1px solid #b2b2b2;
                    @media screen and (max-width: 1000px) {
                        margin-right: 0;
                    }
                    @media screen and (max-width: 510px) {
                        width: 100%;
                    }
                    &::placeholder {
                        font-size: 15px;
                        color: #b2b2b2;
                    }
                    &:last-child {
                        margin-right: 0;
                    }
                }
                .check-reg {
                    display: none;
                }
                .checkMark {
                    position: relative;
                    display: flex;
                    align-items: center;
                    padding-left: 40px;
                    height: 30px;
                    &:before {
                        position: absolute;
                        content: ' ';
                        display: block;
                        width: 30px;
                        height: 30px;
                        border-radius: 3px;
                        border: 2px solid rgb(179, 179, 179);
                        left: 0;
                    }
                }
                .check-reg:checked + .checkMark:before {
                    background-image: url('../img/content/cart/checkmark.svg');
                    background-repeat: no-repeat;
                    background-position: 50% 50%;
                    background-size: 16px auto;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }
        }
    }
}
