.header-nav span {
  position: absolute;
  left: 0;
  top: 0;
  content: "";
  display: block;
  height: 2px;
  background: black;
  width: 100%;
}

.header-nav:before {
  position: absolute;
  left: 0;
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  background: black;
  transition: all .5s;
  width: 66%;
}

.header-nav:after {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  content: "";
  display: block;
  height: 2px;
  background: black;
  transition: transform .5s, width 2s;
}

.header-nav:not(.collapsed) span {
  height: 0;
}

.header-nav:not(.collapsed):before {
  content: "";
  display: block;
  width: 100%;
  transform: rotate(45deg);
  background: black;
  top: 100%;
  margin-top: -45%;
  left: 0;
  margin-left: 0;
}

.header-nav:not(.collapsed):after {
  content: "";
  display: block;
  width: 100%;
  transform: rotate(-45deg);
  background: black;
  top: 100%;
  margin-top: -45%;
  left: 0;
  margin-left: 0;
}

