.goods-cart {
    padding-top: 70px;
    .cart-title {
        margin-bottom: 43px;
    }
    .column {
        .good-row {
            margin-right: 0;
            margin-left: 0;
            padding-bottom: 32px;
            margin-bottom: 34px;
            border-bottom: 1px solid #e8e8e8;
            .good-card {
                display: flex;
                flex: 1 1 auto;
                @media screen and (max-width: 1065px) {
                    flex-direction: column;
                }
                .good-desc {
                    display: flex;
                    flex-direction: row;
                    @media screen and (max-width: 640px) {
                        flex-direction: column;
                    }
                    .img-wrapper {
                        width: 200px;
                        height: 200px;
                        position: relative;
                        margin-right: 55px;
                        @media screen and (max-width: 1065px) {
                            margin-right: 20px;
                        }
                        @media screen and (max-width: 640px) {
                            width: 100%;
                            margin-bottom: 20px;
                            margin-right: 0;
                            display: flex;
                            justify-content: center;
                        }
                        img {
                            width: 100%;
                            height: 100%;
                            @media screen and (max-width: 640px) {
                                width: 200px;
                            }
                        }
                    }
                    .articul-row {
                        display: flex;
                        flex-direction: column;
                        @media screen and (max-width: 1065px) {
                            flex: 1 1 auto;
                        }
                        .articul-title {
                            display: flex;
                            flex-direction: column;
                            @media screen and (max-width: 410px) {
                                text-align: center;
                            }
                            span {
                                &.t-bold {
                                    text-transform: uppercase;
                                    font-size: 22px;
                                    font-weight: 700;
                                    line-height: 1;
                                    color: #000;
                                }
                                &.t-small {
                                    margin-top: 10px;
                                    font-weight: normal;
                                    font-size: 15px;
                                    color: #8d8d8d;
                                }
                            }
                        }
                        .articul-table {
                            display: flex;
                            flex-direction: row;
                            margin-top: 36px;
                            @media screen and (max-width: 1065px) {
                                flex: 1 1 auto;
                                justify-content: space-between;
                            }
                            @media screen and (max-width: 410px) {
                                flex-direction: column;
                            }
                            .table-row {
                                display: flex;
                                flex-direction: column;
                                @media screen and (max-width: 410px) {
                                    text-align: center;
                                    align-items: center;
                                    margin-bottom: 20px;
                                    &:last-child {
                                        margin-bottom: 0;
                                    }
                                }
                                &:nth-child(2) {
                                    margin-left: 100px;
                                    margin-right: 100px;
                                    @media screen and (max-width: 1065px) {
                                        margin-left: 0;
                                        margin-right: 0;
                                    }
                                }
                                .table-header-cell {
                                    font-weight: 700;
                                    font-size: 16px;
                                    color: #000;
                                    margin-bottom: 8px;
                                    line-height: 1;
                                }
                                .table-cell {
                                    font-size: 16px;
                                    font-weight: normal;
                                    color: #000;
                                    .color {
                                        width: 30px;
                                        height: 30px;
                                        display: block;
                                        &.c-blue {
                                            background-color: #43496a;
                                        }
                                        &.c-white {
                                            background-color: #ccd4db;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                .good-price {
                    display: flex;
                    flex-direction: column;
                    margin-right: 0;
                    margin-left: auto;
                    @media screen and (max-width: 1065px) {
                        flex-direction: row-reverse;
                        margin-right: 0;
                        margin-left: 0;
                        margin-top: 20px;
                    }
                    @media screen and (max-width: 410px) {
                        flex-direction: column;
                        align-items: flex-end;
                    }
                    .priceForGood {
                        display: flex;
                        flex-direction: column;
                        align-items: flex-end;
                        @media screen and (max-width: 410px) {
                            margin-bottom: 20px;
                        }
                        .bold-price {
                            display: flex;
                            flex-direction: row;
                            align-items: flex-end;
                            margin-bottom: 22px;
                            @media screen and (max-width: 1065px) {
                                margin-bottom: 10px;
                            }
                            .price {
                                margin-right: 10px;
                                font-size: 26px;
                                font-weight: 700;
                                line-height: 1;
                            }
                            .ruble {
                                font-size: 24px;
                                font-weight: 600;
                                line-height: 1;
                            }
                        }
                        .priceForItem {
                            display: flex;
                            flex-direction: row;
                            align-items: flex-end;
                            .price {
                                margin-right: 8px;
                                font-size: 20px;
                                font-weight: 600;
                                color: #626262;
                            }
                            .ruble {
                                font-family: 'Montserrat', sans-serif;
                                font-size: 17px;
                                font-weight: 500;
                                color: #626262;
                            }
                        }
                    }
                    .amount-and-bin {
                        display: flex;
                        flex-direction: row;
                        flex-wrap: wrap;
                        margin-top: 90px;
                        @media screen and (max-width: 1065px) {
                            margin-top: 0;
                            align-items: center;
                            margin-right: auto;
                            margin-left: 0;
                        }
                        @media screen and (max-width: 410px) {
                            margin-left: 0;
                            margin-right: 0;
                        }
                        .amount {
                            display: flex;
                            flex-direction: row;
                            margin-right: 20px;
                            border: 1px solid #000;
                            border-radius: 3px;
                            align-items: center;
                            input {
                                font-size: 20px;
                                color: #000;
                                text-align: center;
                                border:none;
                                padding:0;
                                width: 75px;
                            }
                            .minus {
                                padding: 0 0 0 20px;
                                background: transparent;
                                border: none;
                                height: 45px;
                                outline: none;
                            }
                            .plus {
                                height: 45px;
                                padding: 0 20px 0 0;
                                background: transparent;
                                border: none;
                                outline: none;

                            }
                        }
                        .bin {
                            .delete-bin {
                                padding: 15px;
                                outline: none;
                                background: #efefef;
                                display: flex;
                                border: none;
                                border-radius: 3px;
                                align-items: center;
                            }

                        }
                    }
                }
            }
        }
    }
}
