.profile-avatar {
    .row {
        @media screen and (max-width: 1445px) {
            justify-content: space-between;
        }
       /* @media screen and (max-width: 1356px) {
            flex-direction: column-reverse;
        }*/
        .profile-desc {
            display: flex;
            flex-direction: column;
            padding-bottom: 80px;
            border-bottom: 1px solid #e8e8e8;
            @include media-breakpoint-down(lg) {
                order:2;
            }
            .profile-title {
                font-weight: 700;
                font-size: 28px;
                color: #000;
                line-height: 1;
                margin-bottom: 39px;
                @media screen and (max-width: 501px) {
                    font-size: calc(15px + 1vw);
                }
                @media screen and (max-width: 340px) {
                    font-size: calc(13px + 1vw);
                }
            }
            .profile-information{
                display: flex;
                flex-direction: row;
                @media screen and (max-width: 1356px) {
                    justify-content: space-between;
                }
                @media screen and (max-width: 1160px) {
                    flex-direction: column;
                }
                .table-wrapper {
                    display: flex;
                    .profInf {
                        display: flex;
                        flex-direction: column;
                        @media screen and (max-width: 501px) {
                            flex: 1 1 auto;
                        }
                        tr {
                            display: grid;
                            grid-template-columns: minmax(0, 255px) minmax(140px, 1fr);
                            margin-bottom: 40px;
                            @media screen and (max-width: 501px) {
                                display: flex;
                                justify-content: space-between;
                            }
                            &:nth-child(2) {
                                margin-bottom: 0;
                            }
                            &:last-child {
                                margin-bottom: 0;
                                margin-top: 80px;
                                @include media-breakpoint-down(xs) {
                                    margin-top: 50px;
                                }
                            }
                            td {
                                font-size: 18px;
                                color: #808080;
                                display: flex;
                                align-items: center;
                                &:last-child {
                                    color: #000;
                                    margin-bottom: 0;
                                }
                                .p-procent {
                                    font-size: 50px;
                                    font-weight: 700;
                                    line-height: 1;
                                    color: #009900;
                                }
                                .span-paragraph {
                                    display: flex;
                                    flex-direction: column;
                                    margin-left: 40px;
                                    @media screen and (max-width: 750px) {
                                        margin-left: 0;
                                        display: none;
                                    }
                                    span {
                                        margin-bottom: 8px;
                                        font-size: 15px;
                                        font-style: italic;
                                        color: #808080;
                                        line-height: 1;
                                        &:last-child {
                                            margin-bottom: 0;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                .button-wrapper {
                    display: flex;
                    align-items: flex-start;
                    margin-left: 50px;
                    @media screen and (max-width: 1356px) {
                        margin-left: 0;
                    }
                    @media screen and (max-width: 1160px) {
                        margin-top: 40px;
                        align-items: unset;
                    }
                    .profile-button {
                        border: none;
                        outline: none;
                        background-color: #efefef;
                        font-weight: 600;
                        font-size: 15px;
                        color: #000;
                        border-radius: 3px;
                        padding: 11.5px 30px;
                        @media screen and (max-width: 1160px) {
                            text-align: center;
                        }
                    }
                }
            }
        }

    }
}


.profile-menu {
    display: flex;
    padding-left: 82px;
    @media screen and (max-width: 1445px) {
        padding-left: 40px;
    }
    @include media-breakpoint-down(lg) {
        padding-left: 15px;
        margin-bottom: 40px;
        order:1;
    }
    .menu-wrapper {
        @media screen and (max-width: 1356px) {
            display: flex;
            flex: 1 1 auto;
        }
        .prof-menu {
            display: flex;
            flex-direction: column;
            list-style: none;
            padding: 0;
            margin-bottom: 0;
            @include media-breakpoint-down(lg) {
                flex-direction: row;
                justify-content: flex-start;
                flex: 1 1 100%;
                flex-wrap: wrap;
            }/*
                    @media screen and (max-width: 501px) {
                        flex-direction: column;
                    }*/
            .prof-menu-item {
                margin-bottom: 20px;
                padding:0;
                /*@media screen and (max-width: 1356px) {
                    margin-bottom: 0;
                }
                @media screen and (max-width: 501px) {
                    margin-bottom: 15px;
                }*/
                @include media-breakpoint-down(lg) {
                    margin-right:15px;
                    margin-bottom:10px;
                }
                &:last-child {
                    margin-bottom: 0;
                }
                .prof-menu-item-href {
                    font-size: 16px;
                    color: #4c4c4c;
                    font-weight: 600;
                }
            }
        }
    }
}