.comparison-row__item{
  @include media-breakpoint-down(xs) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: flex;
  }
}
.comparison-item {
  display: flex;
  align-items: stretch;
  position: relative;
  justify-content: flex-start;
  @include media-breakpoint-down(xs) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .comparison-item__img {
    display: block;
    width: 84px;
    height: 84px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    flex: 0 0 84px;
    margin-right: 16px;
    @include media-breakpoint-down(lg) {
      width: 70px;
      height: 70px;
      flex: 0 0 70px;
    }
    @include media-breakpoint-down(xs) {
      width: 50px;
      height: 50px;
      margin-right: 0;
      flex: 0 0 50px;
      margin-bottom:10px;
    }
  }
  .comparison-item__title {
    text-transform: uppercase;
    font-size: 1.25rem;
    font-weight: 700;
    line-height: 1;
    color: #000;
    @include media-breakpoint-down(lg) {
      font-size: 1.125rem;
    }
    @include media-breakpoint-down(xs) {
      margin-bottom:10px;
      font-size: 1rem;
    }
  }
  .comparison-item__price {
    font-weight: 600;
    font-size: 24px;
    text-transform: uppercase;
    line-height: 1.2;
    font-family: 'Montserrat', sans-serif;
    @include media-breakpoint-down(lg) {
      font-size: 1.125rem;
    }
    @include media-breakpoint-down(xs) {
      font-size: 1rem;
    }
    span {
      font-family: 'Open Sans', sans-serif;
      font-weight: bold;
      font-size: 26px;
      @include media-breakpoint-down(lg) {
        font-size: 1.125rem;
      }
      @include media-breakpoint-down(xs) {
        font-size: 1rem;
      }
    }
  }

}

.comparison-item__add {
  position: relative;
  display: flex;
  font-size: 14px;
  color: $black;
  justify-content: flex-end;
  width: 100%;
  padding: 10px 0 0;
  align-items: center;
  @include media-breakpoint-down(xs) {
    justify-content: center;
    margin-bottom:0;
    margin-top:auto;
  }
  @include media-breakpoint-down(xxs) {
    span{
      display: none;
    }
  }
  &:before {
    content: '';
    width: 20px;
    height: 20px;
    flex: 0 0 20px;
    display: block;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50% 50%;
    margin-right: 8px;
    background-image: url('../img/general/cart.png');
    @include media-breakpoint-down(xs) {
      width: 24px;
      height: 24px;
      flex: 0 0 24px;
    }
  }
}

.section-comparison {
  width: 100%;
  height: auto;
  padding-bottom: 30px;
  overflow: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
  .custom-control-label{
    @include media-breakpoint-down(lg) {
      font-size: 14px;
    }
  }
  &::-webkit-scrollbar {width:0;height:0;}

}

.comparison {
  position: relative;
  overflow: visible;
}
