.breadclumbs {
    padding-top: 24px;
    .bc-r {
        padding-bottom: 20px;
        margin-right: 0;
        margin-left: 0;
        align-items: center;
        border-bottom: 1px solid #e8e8e8;
        .bread-href {
            text-decoration: none;
            &:hover {
                text-decoration: none;
            }
            &.active {
                span {
                    color: #808080;
                }
            }
            span {
                color: #000;
                font-size: 13px;
                font-weight: normal;
            }
        }
        .bread-arrow {
            margin-right: 20px;
            margin-left: 20px;
        }
    }
}
