.banner{
  padding-bottom:53%;
  background-position: 50% 50%;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  @include media-breakpoint-down(sm) {
    padding-bottom:70%;
  }
  @include media-breakpoint-down(xs) {
    padding-bottom:100%;
  }
  .banner__title{
    color:$white;
    position: absolute;
    top:32%;
    left:22%;
    @include media-breakpoint-down(md) {
      top:25%;
      left:10%;
    }
    @include media-breakpoint-down(xs) {
      top:30%;
      left:30px;
    }
  }
}

.main-banner{
    padding:0 0 12px;
    .swiper-pagination{
        bottom: 0;

    }
    .swiper-pagination-bullet {
        background: #000000;
        opacity: .3;
        width: 18px;
        height: 6px;
        border-radius: 3px;
        outline:none !important;
    }
    .swiper-pagination-bullet-active {
        opacity: 1;
        background: #000000;
    }
}
