.common-price {
    padding-top: 55px;
    padding-bottom: 150px;
    @media screen and (max-width: 992px) {
        padding-bottom: 70px;
    }
    .table-wrapper {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        @media screen and (max-width: 510px) {
            align-items: flex-start;
        }
        .takeOrder {
            color: #fff;
            font-size: 14px;
            border-radius: 3px;
            border: none;
            outline: none;
            padding: 16px 62px;
            background-color: #000000;
            margin-top: 70px;
        }
        .c-price {
            display: flex;
            margin-right: 0;
            margin-left: auto;
            text-align: right;
            @media screen and (max-width: 510px) {
                margin-right: 0;
                margin-left: 0;
                flex: 1 1 auto;
                width: 100%;
                text-align: left;
                tbody {
                    width: 100%;
                }
            }
            tr {
                display: grid;
                grid-template-columns: minmax(255px, 1fr) minmax(200px, 1fr);
                margin-bottom: 30px;
                @media screen and (max-width: 510px) {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    flex: 1 1 auto;
                }
                &:last-child {
                    margin-bottom: 0;
                    td {
                        color: #000;
                    }
                }
                td {
                    font-size: 18px;
                    font-weight: 600;
                    color: #808080;
                    display: flex;
                    align-items: flex-end;
                    justify-content: flex-end;
                    .price {
                        font-size: 22px;
                        font-weight: 700;
                    }
                    .ruble {
                        font-size: 20px;
                        font-weight: 600;
                        margin-left: 10px;
                    }
                }
            }
        }
    }
}
