.item__img {
    display: block;
    background-size: cover;
    background-repeat: no-repeat;
    background-color: #efefef;
    background-position: 50% 50%;
    width: 100%;
    padding-bottom: 81.25%;
    margin-bottom: 30px;
    position: relative;
    > div {
        position: absolute;
        top: 50%;
        left: 50%;
        max-width: 98%;
        max-height: 98%;
        text-align: center;
        transform: translate(-50%, -50%);
        font-size: 16px;
        @include media-breakpoint-down(xs) {
            font-size: 12px;
        }
        @include media-breakpoint-down(xxs) {
            font-size: 10px;
        }
    }
}

.item-gallery {
    @include media-breakpoint-down(lg) {
        margin: 0 -10px;
        .row{
            margin: 0 -10px;
        }
        .item-gallery__item {
            padding: 0 10px;
            .item__img {
                margin-bottom: 20px;
            }
        }
    }
}

.item {
    color: #000000;
}

.item__info {
    padding-left: 20px;
    @include media-breakpoint-down(md) {
        padding-left: 0;
    }
}

.item__art {
    font-size: 18px;
    @include media-breakpoint-down(md) {
        font-size: 16px;
    }
}

.item__header {
    border-bottom: 1px solid rgb(232, 232, 232);
    padding-bottom: 28px;
    margin-bottom: 28px;
}

.item__footer {
    padding-top: 48px;
    border-top: 1px solid rgb(232, 232, 232);
}

.item__dop {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .btn-b-filter {
        width: 24px;
        flex: 0 0 24px;
        height: 21px;
    }
    .btn-heart {
        width: 24px;
        flex: 0 0 24px;
        height: 21px;
    }
}

.item__content {
    padding: 70px 0;
    border-bottom: 1px solid rgb(232, 232, 232);
}

.item-prop-list {
    margin: 0 -20px;
}

.item-prop {
    font-size: 18px;
    padding: 0 20px;
    margin-bottom: 50px;
    @include media-breakpoint-down(md) {
        font-size: 16px;
        margin-bottom: 30px;
    }
    @include media-breakpoint-down(xs) {
        margin-bottom: 20px;
    }
    > p {
        margin-bottom: 0;
        padding-bottom: 0;
    }
}

.item-prop_colors {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
}

.color {
    width: 40px;
    height: 40px;
    display: block;
    margin-right: 10px;
    margin-top: 10px;
}

.item__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include media-breakpoint-down(xxs) {
        flex-wrap: wrap;
        flex-direction: column;
        width: 100%;
        align-items: stretch;
        text-align: center;
        .item__footer_price {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 20px;
        }
    }
}

.item-price__old {
    font-weight: 600;
    font-size: 22px;
    text-transform: uppercase;
    line-height: 1.2;
    display: inline-block;
    color: #808080;
    position: relative;
    font-family: 'Montserrat', sans-serif;
    span {
        font-family: 'Open Sans', sans-serif;
        font-size: 28px;
        font-weight: bold;
    }
    &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
        background-color: rgb(255, 0, 0);

        height: 1px;
        transform: rotate(-12deg) translateY(-50%);
    }
}

.item-price {
    font-weight: 600;
    font-size: 30px;
    text-transform: uppercase;
    line-height: 1.2;
    color: $black;
    font-family: 'Montserrat', sans-serif;
    span {
        font-family: 'Open Sans', sans-serif;
        font-size: 36px;
        font-weight: bold;
    }
}
