.choice-of-payment {
    padding-top: 71px;
    
    .cart-title {
        margin-bottom: 54px;
    }
    .form-wrapper {
        display: flex;
        padding-bottom: 67px;
        border-bottom: 1px solid #e8e8e8;
        .paymentForm {
            display: flex;
            flex-direction: row;
            @media screen and (max-width: 1370px) {
                justify-content: space-between;
                flex: 1 1 100%;
            }
            @media screen and (max-width: 992px) {
                flex-direction: column;
            }
            .form-group {
                margin-bottom: 0;
                &:first-child {
                    margin-left: 30px;
                    margin-right: 272px;
                    @media screen and (max-width: 1370px) {
                        margin-left: 0;
                        margin-right: 0;
                    }
                    @media screen and (max-width: 992px) {
                        margin-bottom: 30px;
                    }
                }
                &:last-child {
                    p {
                        display: flex;
                        flex-direction: column;
                        @media screen and (max-width: 580px) {
                            display: block;
                            span {
                                margin-right: 5px;
                                &:last-child {
                                    margin-right: 0;
                                }
                            }
                        }
                    }
                }
                .checkRadio {
                    display: none;
                }
                .checkRadioMark {
                    position: relative;
                    display: flex;
                    align-items: center;
                    font-size: 18px;
                    font-weight: 700;
                    line-height: 1;
                    padding-left: 70px;
                    margin-bottom: 25px;
                    height: 30px;
                    &:before {
                        position: absolute;
                        content: ' ';
                        display: block;
                        width: 30px;
                        height: 30px;
                        border-radius: 3px;
                        border: 2px solid rgb(179, 179, 179);
                        left: 0;
                    }
                }
                p {
                    font-size: 16px;
                    font-weight: normal;
                    margin-left: 70px;
                    margin-bottom: 0;
                    line-height: 1;
                    @media screen and (max-width: 580px) {
                        margin-left: 0;
                    }
                }
                .checkRadio:checked + .checkRadioMark:before {
                    background-image: url('../img/content/cart/checkmark.svg');
                    background-repeat: no-repeat;
                    background-position: 50% 50%;
                    background-size: 16px auto;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }
        }
    }
}
