header {
  padding: 42px 0 30px;
  position: relative;
  z-index: 10;
  background: $white;
  @include media-breakpoint-down(md) {
    padding: 21px 0 15px;
  }
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 46px;

  .btn-icon {
    margin-left: 28px;
    margin-right:0 !important;
    @include media-breakpoint-down(xl) {
      margin-left: 16px;
    }
    @include media-breakpoint-down(xs) {
      margin-left: 16px;
    }
  }

}

.header-logo {
  margin: 0;
  > img {
    width: 255px;
    @include media-breakpoint-down(xl) {
      width: 230px;
    }
    @include media-breakpoint-down(lg) {
      width: 200px;
    }
    @include media-breakpoint-down(xs) {
      width: 120px;
    }
  }
  @include media-breakpoint-down(sm) {
    margin-left:0;
    margin-right: 5px;
  }

}

.header-menu {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  .header-link {
    font-size: 16px;
    color: $black;
    line-height: 1.2;
    font-weight: 600;
    position: relative;
    &:after {
      display: block;
      position: absolute;
      left: 0;
      width: 0;
      height: 2px;
      background-color: $black;
      content: "";
      transition: width 0.3s ease-out;
    }
    &:hover,&:focus{
      text-decoration: none;
    }
    &:hover:after,
    &:focus:after {
      width: 100%; /*устанавливаем значение 100% чтобы ссылка подчёркивалась полностью*/
    }
  }
  .header-item {
    padding: 0 29px;
    margin: 0;
    @include media-breakpoint-down(xl) {
      padding: 0 15px;
    }
    @include media-breakpoint-down(lg) {
      padding: 0 8px;
    }

  }
}

