.header-nav {
  width: 24px;
  height: 16px;
  margin: 0 29px 0 50px;
  position: relative;
  flex:0 0 24px;
  box-sizing: border-box;
  //transition: all .5s;
  cursor: pointer;
  @include media-breakpoint-down(xl) {
    margin: 0 30px;
  }
  @include media-breakpoint-down(lg) {
    margin: 0 15px 0 30px;
  }
  @include media-breakpoint-down(sm) {
    order:4;
    margin: 0 0 0 30px;
  }
  @include media-breakpoint-down(xs) {
    margin-left: 5px;
  }

  span {
    position: absolute;
    left: 0;
    top: 0;
    content: "";
    display: block;
    height: 2px;
    background: black;
    width: 100%;
  }
  &:before {
    position: absolute;
    left: 0;
    bottom: 0;
    content: "";
    display: block;
    height: 2px;
    background: black;
    transition: all .5s;
    width: 66%;
  }
  &:after {
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    content: "";
    display: block;
    height: 2px;
    background: black;
    transition: transform .5s, width 2s;
  }
  &:not(.collapsed) {
    span{
      height: 0;
    }

    &:before {
      content: "";
      display: block;
      width: 100%;
      transform: rotate(45deg);
      background: black;
      top: 100%;
      margin-top: -45%;
      left: 0;
      margin-left: 0;
    }
    &:after {
      content: "";
      display: block;
      width: 100%;
      transform: rotate(-45deg);
      background: black;
      top: 100%;
      margin-top: -45%;
      left: 0;
      margin-left: 0;
    }
  }
}

.header-location {
  position: relative;
  display: flex;
  margin: 0 30px;
  @include media-breakpoint-down(xl) {
    margin: 0 15px;
  }
  &:hover{
    text-decoration: none;
    span {
      border-bottom: 1px solid $black;

    }
  }
  span {
    font-size: 15px;
    color: $black;
    line-height: 1.2;
    border-bottom: 1px dashed $black;
  }
  &:before {
    content: '';
    width: 20px;
    height: 20px;
    background-position: 50% 50%;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url('../img/general/map.png');
    margin-right: 12px;
    display: block;
  }
}

#collapseHeader {
  position: absolute;
  bottom: 5px;
  left: 0;
  width: 100%;
  background: #fff;
  z-index: 10;
  transform: translateY(100%);
}

.collapse__header {
  padding: 90px 0 150px;
  @include media-breakpoint-down(lg) {
    padding: 50px 0 80px;
  }
  @include media-breakpoint-down(md) {
    padding: 30px 0 50px;
  }

}
.bottom-menu {
    position: fixed;
    display: flex;
    bottom: 0;
    left: 0;
    right: 0;
    height: 60px;
    background: #fff;
    justify-content: space-between;
    align-items: center;
    padding: 5px 15px;
    box-shadow: -20px 0 20px rgba(0, 0, 0, .2);

}
.input-group {
    left: auto;
    transition: left .5s ease-in;
}
.wide-input  {
    width: calc(100% - 65px);
    left: 0;
    position: absolute;
    z-index: 10;
    transition: left .5s cubic-bezier(1, 0, 1, 0);

}
