.page__footer{
  background: $gray-100;
  padding:100px 0 48px;
  font-size: 14px;
  line-height: 1.286;
  @include media-breakpoint-down(md) {
    padding:50px 0 40px;
  }
  .search{
    max-width: 100%;
  }
  a{
    color:$black;
  }h4{
     margin-bottom:24px;
   }

  .search{
    background: $white;
  }


  .footer-row{
    align-items: stretch;
  }
}
.menu__last{
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  flex-direction: column;
  @include media-breakpoint-down(md) {
    justify-content: flex-start;
    align-items: flex-start;
  }

}
.menu{
  padding:0;
  list-style-type: none;
  margin:0;
  padding-bottom:20px;
  font-size: 14px;
  line-height: 1.286;
  li{
    margin-bottom:20px;
    @include media-breakpoint-down(md) {
      margin-bottom:8px;
    }
    &:last-child{
      margin:0;
    }
  }
  a{
    color:$black;
    &:hover, &:focus{
      text-decoration: none;
      border-bottom:1px solid $black;
    }
  }

}

.footer-bottom{
  padding-top:50px;
  margin-top:36px;
  border-top:1px solid $white;
}
