.card-list {
    > div {
        margin-bottom: 30px;
    }
    .card-list_item {
        margin-bottom: 50px;
    }
}

.card {
    height: 100%;
    color: $black;
    border: none;
    display: block;
    font-size: 15px;
    border-radius: 0;
    a {
        &:hover, &:focus {
            text-decoration: none;
        }
    }
    h5 {
        color: $black;
    }
    position: relative;
    @include media-breakpoint-down(md) {
        font-size: 14px;
    }
    .card__img {
        display: block;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50% 50%;
        width: 100%;
        //height: 390px;
        padding-bottom: 81.25%;
        margin: 0;
        @include media-breakpoint-down(md) {
            padding-bottom: 60%;
        }
    }
    .card__desc {
        > p {
            margin: 0;
        }
    }
    .card__arrow {
        position: absolute;
        display: block;
        width: 22px;
        height: 22px;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: 50% 50%;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        background-image: url('../img/general/arrow.png');
        @include media-breakpoint-down(xxs) {
            display: none;
        }
    }
    .card__caption {
        margin: 30px 30px 22px;
        position: relative;
        &.card__caption-arrow {
            padding-right: 40px;
            @include media-breakpoint-down(xxs) {
                padding-right: 0;
            }
        }
        @include media-breakpoint-down(md) {
            margin: 20px;
        }
        @include media-breakpoint-down(xxs) {
            text-align: center;
        }
    }
    .card__art {
        color: $gray-700;
        margin-bottom: 12px;
    }
    .card__price {
        font-weight: 600;
        font-size: 30px;
        text-transform: uppercase;
        line-height: 1.2;
        font-family: 'Montserrat', sans-serif;
        span {
            font-family: 'Open Sans', sans-serif;
            font-weight: bold;
            font-size: 36px;
        }
    }
    .cart__add {
        position: relative;
        display: flex;
        font-size: 14px;
        color: $black;
        align-items: center;
        &:before {
            content: '';
            width: 20px;
            height: 20px;
            flex: 0 0 20px;
            display: block;
            background-repeat: no-repeat;
            background-size: contain;
            background-position: 50% 50%;
            margin-right: 8px;
            background-image: url('../img/general/cart.png');

        }
    }
    .cart__download {
        position: relative;
        display: flex;
        font-size: 14px;
        color: $black;
        align-items: center;
        @include media-breakpoint-down(xxs) {
            justify-content: center;
        }
        span {
            text-decoration: underline;
        }
        &:before {
            content: '';
            width: 20px;
            height: 20px;
            flex: 0 0 20px;
            display: block;
            background-repeat: no-repeat;
            background-size: contain;
            background-position: 50% 50%;
            margin-right: 14px;
            background-image: url('../img/general/download.png');

        }
    }
    .card__bottom {
        margin-top: 12px;
        align-items: center;
        justify-content: space-between;
        @include media-breakpoint-down(xxs) {
            flex-direction: column;
            > div:not(:last-child) {
                margin-bottom: 16px;
            }
        }
    }
    .card-properties {
        display: flex;
        align-items: stretch;
        margin: 12px -6px;
        justify-content: flex-start;
        flex-wrap: wrap;
        position: relative;
        .card-properties__item {
            flex: 1 0 50%;
            width: 50%;
            height: auto;
            padding: 6px;

            @include media-breakpoint-only(lg) {
                flex: 1 1 100%;
                width: 100%;
            }
            @include media-breakpoint-down(xs) {
                flex: 1 1 100%;
                width: 100%;
            }
        }
        .card-properties__body {
            text-align: center;
            padding: 10px 5px;
            height: 100%;
            border-radius: 3px;
            p {
                margin: 0;
                padding: 0
            }
            p:not(:last-child) {
                @include media-breakpoint-up(lg) {
                    padding-bottom: 6px;
                }
            }
        }
    }

    &_grey {
        background: $gray-300;
        .card-properties__body {
            background: $gray-800;
        }
    }
    &_white {
        background: white;
        border-bottom: 1px solid rgb(232, 232, 232);
        .card-properties__body {
            border: 1px solid rgb(230, 230, 230);

        }
        .card__caption {
            margin: 30px 30px 40px;
            @include media-breakpoint-down(md) {
                margin: 20px 20px 30px;
            }
        }
    }
    &_main {
        border: 0;
        .card__img {
            background-size: contain;
        }
        .card__caption {
            margin: 16px 10px;
            text-align: center;
        }
    }
    &_small {
        .card__caption {
            margin: 38px 50px;
            @include media-breakpoint-down(md) {
                margin: 30px 20px;
            }
        }
        .card__img {
            //height: 480px;
            padding-bottom: 100%;
            @include media-breakpoint-down(md) {
                padding-bottom: 80%;
            }
        }
    }
    &_cat {
        .card-properties {
            .card-properties__item {
                flex: 1 0 50%;
                width: 50%;
                @include media-breakpoint-only(lg) {
                    flex: 1 1 100%;
                    width: 100%;
                }
                @include media-breakpoint-down(xs) {
                    flex: 1 1 100%;
                    width: 100%;
                }
            }
        }
    }
    &_big {
        .card__caption {
            margin: 50px 50px 70px;
            @include media-breakpoint-down(md) {
                margin: 30px 20px;
            }
        }
        .card__arrow {
            top: 30px;
        }
        .card__img {
            padding-bottom: 53.06%;
            @include media-breakpoint-down(md) {
                padding-bottom: 60%;
            }
        }
    }
}
